import React from "react";
import { useNavigate } from "react-router-dom";
import styles from "./AdminLogin.module.css";
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { Button, Input } from "../../../components";
import { useAdminLogin } from "../../../api/hooks";

const schema = yup
  .object({
    password: yup.string().required(" "),
  })
  .required();

export const AdminLogin = () => {
  const navigate = useNavigate();
  const adminLogin = useAdminLogin();

  const {
    handleSubmit,
    control,
    formState: { errors, isSubmitting },
  } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      password: "",
    },
  });

  const onSubmit = async (data) => {
    try {
      const loginData = {
        id: 2,
        password: data.password.trim(),
      };

      console.log("Submitting data:", loginData);
      const response = await adminLogin.mutateAsync(loginData);

      if (response?.data?.access_token) {
        navigate("/admin/writer");
      }
    } catch (error) {
      console.error("Login error:", error.response?.data || error);
    }
  };

  return (
    <div className={styles.loginBackground}>
      <section className={styles.formWrap}>
        <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
          <div className={styles.formTitleWrap}>
            <h2> </h2>
            <p>       </p>
          </div>

          <div className={styles.inputWrap}>
            <label htmlFor="password" className={styles.inputLabel}>
              
            </label>
            <Controller
              name="password"
              control={control}
              render={({ field }) => (
                <Input
                  {...field}
                  type="password"
                  placeholder=" "
                />
              )}
            />
            {errors.password && (
              <p className={styles.errorMessage}>{errors.password.message}</p>
            )}
          </div>

          {adminLogin.error && (
            <p className={styles.errorMessage}>
               .  .
            </p>
          )}

          <Button
            variant="default"
            size="full"
            type="submit"
            label={adminLogin.isLoading ? " .." : ""}
            disabled={adminLogin.isLoading || isSubmitting}
          />
        </form>
      </section>
    </div>
  );
};
