import React, { useState, useCallback, useEffect, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./OrderList.module.css";
import {
  EventSelectionModal,
  Filter,
  Pagination,
  Tab,
  TabNavigation,
} from "../../../modules";
import { Button } from "../../../components";
import {
  useCurrentEvents,
  useOrders,
  useAuthors,
  useAffiliations,
  useUpdateOrderStatus,
} from "../../../api/hooks";
import { utils, write } from 'xlsx';

const getCollectionMethod = (method) => {
  switch (method) {
    case "Delivery":
      return "";
    case "Pickup on site":
      return "";
    case "Pickup in store":
      return "";
    default:
      return method;
  }
};

const getKoreanStatus = (status) => {
  const statusMap = {
    'Counsel': '',
    'Order Completed': '',
    'Packaging Completed': '',
    'Repair Received': '',
    'Repair Completed': '',
    'In delivery': '',
    'Delivery completed': '',
    'Receipt completed': '',
    'Accommodation': ''
  };
  return statusMap[status] || status;
};

const formatCurrency = (amount) => {
  return amount ? amount.toLocaleString('ko-KR') : '0';
};

const exportOrdersToExcel = (orders, authors, affiliations, filename = 'orders.xlsx') => {
  //    
  const headers = {
    id: 'ID',
    author: '',
    groomName: '',
    brideName: '',
    affiliation: '',
    collectionMethod: '',
    status: '',
    created_at: '',
    totalPrice: '',
    totalPayment: '',
    payer: '',
    address: '',
    event_name: '',
    products: '',
    notes: '',
    alter_notes: ''
  };

  //  
  const excelData = orders.map(order => ({
    id: order.id || '',
    author: authors[order.author_id] || order.author_id || '',
    groomName: order.groomName || '',
    brideName: order.brideName || '',
    affiliation: affiliations[order.affiliation_id] || order.affiliation_id || '',
    collectionMethod: getCollectionMethod(order.collectionMethod) || '',
    status: getKoreanStatus(order.status) || '',
    created_at: order.created_at ? new Date(order.created_at).toLocaleDateString('ko-KR') : '',
    totalPrice: formatCurrency(order.totalPrice),
    totalPayment: formatCurrency(order.advancePayment + order.balancePayment),
    payer: order?.payments?.[0]?.payer || '',
    address: order.address || '',
    event_name: order.event_name || '',
    products: order.orderItems?.map((item, index) => 
      `${index + 1}: ${item.product.name}, : ${item.attributes[0]?.value || '-'}, : ${item.quantity}`
    ).join('\n') || '',
    notes: order.notes || '',
    alter_notes: order.alter_notes || ''
  }));

  //  
  const ws = utils.json_to_sheet([
    Object.values(headers), //  
    ...excelData.map(row => Object.values(row)) //  
  ], { skipHeader: true });

  //  
  const wb = utils.book_new();
  utils.book_append_sheet(wb, ws, ' ');

  //    
  const maxWidth = {};
  Object.keys(headers).forEach((key, index) => {
    maxWidth[index] = Math.max(
      headers[key].length * 2, //  
      ...excelData.map(row => {
        const cellContent = String(row[key] || '');
        const lines = cellContent.split('\n');
        return Math.max(...lines.map(line => line.length));
      })
    );
  });

  //   
  ws['!cols'] = Object.values(maxWidth).map(width => ({
    width: Math.min(Math.max(width, 8), 50) //  8,  50
  }));

  //   
  const range = utils.decode_range(ws['!ref']);
  for (let R = range.s.r; R <= range.e.r; R++) {
    for (let C = range.s.c; C <= range.e.c; C++) {
      const cell_address = utils.encode_cell({ r: R, c: C });
      const cell = ws[cell_address];
      
      if (!cell) continue;

      //   
      cell.s = {
        alignment: {
          vertical: 'center',
          horizontal: 'center',
          wrapText: true
        },
        border: {
          top: { style: 'thin' },
          bottom: { style: 'thin' },
          left: { style: 'thin' },
          right: { style: 'thin' }
        },
        font: { name: ' ' }
      };

      //   
      if (R === 0) {
        cell.s.fill = { fgColor: { rgb: "FFE5E5E5" } };
        cell.s.font.bold = true;
      }

      //    ( )
      if (['', ''].includes(headers[Object.keys(headers)[C]])) {
        cell.s.alignment.horizontal = 'right';
      }

      //     ( )
      if (['', '', ''].includes(headers[Object.keys(headers)[C]])) {
        cell.s.alignment.horizontal = 'left';
      }
    }
  }

  //   
  const excelBuffer = write(wb, { 
    bookType: 'xlsx', 
    type: 'array',
    cellStyles: true 
  });
  
  const blob = new Blob([excelBuffer], { 
    type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
  });
  
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
};

export const OrderList = () => {
  const navigate = useNavigate();
  const [isEventSelectionModalOpen, setIsEventSelectionModalOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  const initialFilters = {
    search: "",
    status: "",
    order_date_from: null,
    order_date_to: null,
    is_temp: false,
    event_name: "",
    sort: "order_date_desc",
  };

  const [filters, setFilters] = useState(initialFilters);
  const itemsPerPage = 10;

  //  fetching hooks
  const { data: events } = useCurrentEvents();
  
  //    
  const { data: ordersData, isLoading: ordersLoading } = useOrders(
    {
      ...filters,
      limit: itemsPerPage,
      offset: (currentPage - 1) * itemsPerPage,
    },
    {
      enabled: true,
      refetchOnWindowFocus: false,
    }
  );

  //     
  const { data: allOrdersData, isLoading: allOrdersLoading } = useOrders(
    {
      ...filters,
      limit: 999999,  //    
      offset: 0,
    },
    {
      enabled: true,
      refetchOnWindowFocus: false,
    }
  );

  const { data: authorsData } = useAuthors();
  const { data: affiliationsData } = useAffiliations();
  const updateOrderStatusMutation = useUpdateOrderStatus();

  //   
  const authors = useMemo(
    () =>
      authorsData?.reduce(
        (acc, author) => ({
          ...acc,
          [author.id]: author.name,
        }),
        {}
      ) || {},
    [authorsData]
  );

  const affiliations = useMemo(
    () =>
      affiliationsData?.reduce(
        (acc, affiliation) => ({
          ...acc,
          [affiliation.id]: affiliation.name,
        }),
        {}
      ) || {},
    [affiliationsData]
  );

  const orders = ordersData?.orders || [];
  const total = ordersData?.total || 0;

  // filters  
  useEffect(() => {
    console.log("Filters changed:", filters);
    console.log("allOrdersData", allOrdersData);
    console.log("authors", authors)
    console.log("affiliations", affiliations);
    
  }, [filters]);

  // Event Handlers
  const handleEventSelection = useCallback(
    (eventId, eventName) => {
      navigate(`/admin/order/create/${eventId}`);
      setIsEventSelectionModalOpen(false);
    },
    [navigate]
  );

  const handlePageChange = useCallback((page) => {
    setCurrentPage(page);
  }, []);

  const handleSort = useCallback((key) => {
    setFilters((prev) => ({
      ...prev,
      sort: prev.sort === `${key}_asc` ? `${key}_desc` : `${key}_asc`,
    }));
  }, []);

  const handleOrderStatusChange = useCallback(
    async (event, orderId) => {
      event.stopPropagation();
      const newStatus = event.target.value;
      try {
        await updateOrderStatusMutation.mutateAsync({
          orderId,
          status: newStatus,
        });
      } catch (error) {
        console.error("Failed to update order status:", error);
        alert("   .  .");
      }
    },
    [updateOrderStatusMutation]
  );

  const handleExcelDownload = useCallback(async () => {
    try {
      const allOrders = allOrdersData?.orders || [];
      exportOrdersToExcel(
        allOrders,
        authors,
        affiliations,
        `_${new Date().toLocaleDateString()}.xlsx`
      );
    } catch (error) {
      console.error("Excel download failed:", error);
      alert("  .  .");
    }
  }, [allOrdersData, authors, affiliations]);

  const handleTabChange = useCallback((tabName) => {
    setFilters((prev) => ({
      ...initialFilters,
      event_name: prev.event_name,
      is_temp: tabName === "tab2",
    }));
    setCurrentPage(1);
  }, []);

  const handleRowClick = useCallback(
    (order) => {
      navigate(`/admin/order/${order.event_id}/${order.id}`);
    },
    [navigate]
  );

  const renderTable = useCallback(
    (items) => (
      <table className={styles.table}>
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th
              scope="col"
              onClick={() => handleSort("order_date")}
              style={{ cursor: "pointer" }}
            >
              {" "}
              {filters.sort?.includes("order_date") &&
                (filters.sort === "order_date_asc" ? "" : "")}
            </th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {ordersLoading ? (
            <tr>
              <td colSpan="12" className={styles.loadingMessageWrap}>
                  ...
              </td>
            </tr>
          ) : items.length === 0 ? (
            <tr>
              <td colSpan="12" className={styles.noDataMessageWrap}>
                  .
              </td>
            </tr>
          ) : (
            items.map((order) => (
              <tr
                key={order.id}
                onClick={() => handleRowClick(order)}
                className={styles.orderLink}
              >
                <td>{order.id || "-"}</td>
                <td>{authors?.[order.author_id] || order.author_id || "-"}</td>
                <td>{order.groomName || "-"}</td>
                <td>{order.brideName || "-"}</td>
                <td>
                  {affiliations[order.affiliation_id] ||
                    order.affiliation_id ||
                    "-"}
                </td>
                <td>{getCollectionMethod(order.collectionMethod) || "-"}</td>
                <td>
                  <select
                    name={`order_${order.id}`}
                    value={order.status}
                    onChange={(event) =>
                      handleOrderStatusChange(event, order.id)
                    }
                    onClick={(e) => e.stopPropagation()}
                    className={styles.orderStatus}
                  >
                    <option value="Counsel"></option>
                    <option value="Order Completed"></option>
                    <option value="Packaging Completed"></option>
                    <option value="Repair Received"></option>
                    <option value="Repair Completed"></option>
                    <option value="In delivery"></option>
                    <option value="Delivery completed"></option>
                    <option value="Receipt completed"></option>
                    <option value="Accommodation"></option>
                  </select>
                </td>
                <td>
                  {new Date(order.created_at).toLocaleDateString() || "-"}
                </td>
                <td>{order.totalPrice || "-"}</td>
                <td>{order.advancePayment + order.balancePayment || "-"}</td>
                <td>{order.payments?.[0]?.payer || "-"}</td>
                <td>{order.address || "-"}</td>
                <td>{order.event_name || "-"}</td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    ),
    [
      filters,
      ordersLoading,
      authors,
      affiliations,
      handleSort,
      handleOrderStatusChange,
      handleRowClick,
    ]
  );

  return (
    <div className={styles.adminLayout}>
      <TabNavigation />
      <main className={styles.adminMainWrap}>
        <h2 className={styles.adminTitle}> </h2>
        <section className={styles.section}>
          <Tab defaultActiveTab="tab1" onTabChange={handleTabChange}>
            <Tab.TabPane name="tab1" tab=" ">
              <Filter
                filters={filters}
                setFilters={setFilters}
                isAdminPage={true}
              />
              <div className={styles.actionButtonsWrap}>
                <Button
                  label="Excel "
                  className={styles.excelButton}
                  onClick={handleExcelDownload}
                  disabled={ordersLoading || allOrdersLoading}
                />
                <Button
                  label=" "
                  className={styles.newOrderButton}
                  onClick={() => setIsEventSelectionModalOpen(true)}
                />
              </div>
              {renderTable(orders)}
              <Pagination
                className={styles.pagination}
                totalItems={total}
                itemsPerPage={itemsPerPage}
                currentPage={currentPage}
                onPageChange={handlePageChange}
              />
            </Tab.TabPane>
            <Tab.TabPane name="tab2" tab=" ">
              <Filter
                filters={filters}
                setFilters={setFilters}
                isAdminPage={true}
              />
              <div className={styles.actionButtonsWrap}>
                <Button
                  label="Excel "
                  className={styles.excelButton}
                  onClick={handleExcelDownload}
                  disabled={ordersLoading || allOrdersLoading}
                />
                <Button
                  label=" "
                  className={styles.newOrderButton}
                  onClick={() => setIsEventSelectionModalOpen(true)}
                />
              </div>
              {renderTable(orders)}
              <Pagination
                className={styles.pagination}
                totalItems={total}
                itemsPerPage={itemsPerPage}
                currentPage={currentPage}
                onPageChange={handlePageChange}
              />
            </Tab.TabPane>
          </Tab>
        </section>
      </main>
      <EventSelectionModal
        isOpen={isEventSelectionModalOpen}
        onClose={() => setIsEventSelectionModalOpen(false)}
        onSelectEvent={handleEventSelection}
        events={events || []}
        navigate={navigate}
      />
    </div>
  );
};